import React, { useState } from 'react';
import axios from 'axios';
import '../css/RegistrationForm.css';
import Spinner from './Spinner';
import Swal from 'sweetalert2';
import { Link, useNavigate } from 'react-router-dom';

const RegistrationForm = ({ onRegister }) => {
  const navigate = useNavigate();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [email, setEmail] = useState('');
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);

  const handleRegister = async () => {
    const errors = {};

    if (!username.trim()) {
      errors.username = 'Username is required';
    } else if (!/^[a-zA-Z][\w!@#$%^&*]+$/g.test(username)) {
      errors.username = 'Username should start with a letter and can contain alphanumeric characters and special characters';
    }

    if (!email.trim()) {
      errors.email = 'Email is required';
    } else if (!/^\S+@\S+\.\S+$/.test(email)) {
      errors.email = 'Invalid email address';
    }

    if (!password.trim()) {
      errors.password = 'Password is required';
    } else if (password.length < 6) {
      errors.password = 'Password must be at least 6 characters long';
    }

    // If there are errors, set them in state and prevent form submission
    if (Object.keys(errors).length > 0) {
      setErrors(errors);
      return;
    }

    try {
      setLoading(true);
      const response = await axios.post('/register', { username, email, password });
      if (response.data.exists) {
        Swal.fire('Username already exists. Please try another username.');
        return;
      }
      Swal.fire("Registered successfully");
      setUsername('');
      setPassword('');
      setEmail('');
      navigate('/login');
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="box">
      {loading ? (
        <Spinner />
      ) : (
       <div className="form-container">
      <h2>Register</h2>
      <input
        type="text"
        placeholder="Username"
        value={username}
        onChange={(e) => setUsername(e.target.value)}
      />
      {errors.username && <span className="error">{errors.username}</span>}
      <input
        type="email"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />
      {errors.email && <span className="error">{errors.email}</span>}
      <input
        type="password"
        placeholder="Password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      />
      {errors.password && <span className="error">{errors.password}</span>}
      <button onClick={handleRegister}>Register</button>
      <h3>
        Already a user? <Link to="/login">Login</Link>
      </h3>
    </div>
      )}
    </div>
  );
};

export default RegistrationForm;
