import React, { useState } from 'react';
import axios from 'axios';
import '../css/LoginForm.css';
import Spinner from './Spinner';
import Swal from 'sweetalert2';
import { Link, useNavigate } from 'react-router-dom';

const LoginForm = ({ onLogin }) => {
  const navigate = useNavigate();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [showForm, setShowForm] = useState(true);
  const [errors, setErrors] = useState({});

  const handleLogin = async () => {
    try {
      setLoading(true);

      // Perform form validation
      const errors = {};

      if (!username.trim()) {
        errors.username = 'Username is required';
      }

      if (!password.trim()) {
        errors.password = 'Password is required';
      }

      if (Object.keys(errors).length > 0) {
        setErrors(errors);
        setLoading(false);
        return;
      }

      const response = await axios.post('/login', { username, password });

      localStorage.setItem('username', username);
      Swal.fire({
        icon: 'success',
        text: `Welcome 🎊🎊 Logged in Successfully!`,
      });
      setLoading(false);

      onLogin(); // Call the onLogin function to set the isLoggedIn state in App.js
      navigate('/home');
    } catch (error) {
      console.error(error);
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: 'Something went wrong',
      });
      setLoading(false);
    }
  };

  return (
    <>
      <div className="box">
        {loading ? (
          <Spinner /> // Render the spinner when loading is true
        ) : (
          showForm && ( // Render the form only if showForm is true
            <div className="form-container">
              <h2>Login</h2>
              <input
                type="text"
                placeholder="Username"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
              />
              {errors.username && <span className="error">{errors.username}</span>}
              <input
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
              {errors.password && <span className="error">{errors.password}</span>}
              <button onClick={handleLogin}>Login</button>
              <h3>
                <Link to="/forgot-password">Forgot Password ??</Link>
              </h3>
              <h3>
                Not a user? Want to <Link to="/register">Register</Link>..
              </h3>
            </div>
          )
        )}
      </div>
    </>
  );
};

export default LoginForm;
