import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { FcApproval } from 'react-icons/fc';
import Swal from 'sweetalert2';
import {message} from 'antd';
import '../css/TransactionList.css';
import '../css/TransactionSummary.css';
import TopHeader from './TopHeader';

const TransactionList = () => {
  const username = localStorage.getItem('username');
  const [transactions, setTransactions] = useState([]);
  const [description, setDescription] = useState('');
  const [amount, setAmount] = useState('');
  const [filter, setFilter] = useState('all');
  const [type, setType] = useState('income');
  const [limitAmount, setLimitAmount] = useState('');
  const [limitType, setLimitType] = useState('');
  const [showForm, setShowForm] = useState(false);
  const [errors, setErrors] = useState({});

  useEffect(() => {
    fetchTransactions();
  }, []);

  useEffect(() => {
    checkLimit();
  }, [transactions]);

  const fetchTransactions = async () => {
    try {
      const response = await axios.get('/transactions');
      setTransactions(response.data);
    } catch (error) {
      console.error(error);
    }
  };

  const handleCreateTransaction = async () => {
    try {
      if (!validateForm()) {
        return;
      }
      

      await axios.post('/transactions', { description, amount, type });
      fetchTransactions();
      message.success("Transaction Created Successfully ")
      setDescription('');
      setAmount('');
      setType('income');
      setFilter('all');
    } catch (error) {
      console.error(error);
    }
  };

  const handleDelete = async (transactionId) => {
    try {
      await axios.delete(`/transactions/${transactionId}`);
      fetchTransactions();
      message.success("Transaction deleted successfully")
    } catch (error) {
      console.error(error);
    }
  };

  const handleUpdate = async (transactionId, updatedTransaction) => {
    try {
      await axios.put(`/transactions/${transactionId}`, updatedTransaction);
      fetchTransactions();
      message.success("Transaction updated succcessfully")
    } catch (error) {
      console.error(error);
    }
  };

  const filteredTransactions = filter === 'all'
    ? transactions
    : transactions.filter((transaction) => transaction.type === filter);

  const calculateTotal = (transactionType) => {
    const filteredByType = transactions.filter((transaction) => transaction.type === transactionType);
    const totalAmount = filteredByType.reduce((sum, transaction) => sum + parseFloat(transaction.amount), 0);
    return totalAmount.toFixed(2);
  };

  const checkLimit = () => {
    if (limitAmount && limitType) {
      const totalAmount = calculateTotal(limitType);
      if (parseFloat(totalAmount) >= parseFloat(limitAmount)) {
        Swal.fire({
          icon: 'success',
          title: `Hey, ${username}`,
          text: `Your ${limitType} limit of ${limitAmount} has been reached!`,
        });
      }
    }
  };

  const showLimitForm = () => {
    setShowForm(true);
  };

  const hideLimitForm = () => {
    setShowForm(false);
  };

  const validateForm = () => {
    const errors = {};

    if (!description.trim()) {
      errors.description = 'Description is required';
    }

    if (!amount.trim()) {
      errors.amount = 'Amount is required';
    } else if (isNaN(amount) || parseFloat(amount) <= 0) {
      errors.amount = 'Amount must be a positive number';
    }

    setErrors(errors);

    return Object.keys(errors).length === 0;
  };

  return (
    <>
      <TopHeader />
     <div className='username' >
     <p>Welcome,<span className='name'>&nbsp;{username}</span></p>
    <br/>
     </div>
      <div className='transaction-summary'>
        <div className="summary-item">
          <p>Total Income</p>
          <p className="income">{calculateTotal('income')}</p>
        </div>
        <div className="summary-item">
          <p>Total Expense</p>
          <p className="expense">{calculateTotal('expense')}</p>
        </div>
        <div className="summary-item">
          <p>Total Savings</p>
          <p className="saving">{calculateTotal('savings')}</p>
        </div>
      </div>
      
      <div className="outer-container">
       <div className='box-2'>
       <div className="form-container">
          <h2>Create Transaction</h2>
          <input
            type="text"
            placeholder="Description"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
          {errors.description && <span className="error">{errors.description}</span>}
          <input
            type="number"
            placeholder="Amount"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
          />
          {errors.amount && <span className="error">{errors.amount}</span>}
          <select value={type} onChange={(e) => setType(e.target.value)}>
            <option value="income">Income</option>
            <option value="expense">Expense</option>
            <option value="savings">Savings</option>
          </select>
          <button onClick={handleCreateTransaction}>Create Transaction</button>
          <div>
          {showForm ? (
            <>
                 <div className="modal-overlay">
          <div className="modal-dialog">
            <div className='list-form-container'>
             <div className='top'>
             <h2>Set Limit </h2>
              <h3 className="close-button" onClick={() => setShowForm(false)}>
              X
            </h3>
             </div>
              <select className='input' value={limitType} onChange={(e) => setLimitType(e.target.value)}>
                <option value="type">Select Type</option>
                <option value="expense">Expense</option>
                <option value="savings">Savings</option>
              </select>
              <input
                className='input'
                type="number"
                placeholder="Amount Limit"
                value={limitAmount || ''}
                onChange={(e) => setLimitAmount(parseFloat(e.target.value))}
              />
              <button onClick={hideLimitForm}>Set Limit</button>
            </div>
          </div>
        </div>
              
            </>
          ) : (
           <>
           <br/>
            <p className='line'>----------------------Or-------------------</p>
            <button className='budget' onClick={showLimitForm}>Set Budget Limit</button>
            </>
          )}
        </div>
        </div>
      
       </div>
        
        <div className="transaction-list-container">
          <h2>Transactions</h2>
          <div className="filter-container">
            <button className={filter === 'all' ? 'active' : ''} onClick={() => setFilter('all')}>
              All
            </button>
            <button className={filter === 'income' ? 'active' : ''} onClick={() => setFilter('income')}>
              Income
            </button>
            <button className={filter === 'expense' ? 'active' : ''} onClick={() => setFilter('expense')}>
              Expense
            </button>
            <button className={filter === 'savings' ? 'active' : ''} onClick={() => setFilter('savings')}>
              Savings
            </button>
            
          </div>
          <div className="transaction-item">
            {Array.isArray(filteredTransactions) && filteredTransactions.length > 0 ? (
              filteredTransactions.map((transaction) => (
                <div className="item" key={transaction._id}>
                  <h4 className="description">{transaction.description}</h4>
                  <h4 className="amount">{transaction.amount}</h4>
                  <div className="button-gp">
                    <button onClick={() => handleUpdate(transaction._id, { amount: prompt('Enter new amount') })}>
                      <FcApproval className="icon" />
                    </button>
                    <button onClick={() => handleDelete(transaction._id)}>
                      <h4 className="delete-icon">X</h4>
                    </button>
                  </div>
                </div>
              ))
            ) : (
              <p>No transactions available.</p>
            )}
          </div>
        </div>
       
      </div>
    </>
  );
};

export default TransactionList;
